import React from "react"
import Media from "react-responsive"
import { Container } from "react-bootstrap"
import Slider from "react-slick"

import Layout from "../components/layout"
import SEO from "../components/seo"

import "react-image-gallery/styles/css/image-gallery.css"
import image1L from "../images/belvedere-firepit/belvedere-firepit-1L.jpg"
import image2L from "../images/bel-marin-keys/bel-marin-4P.jpg"
import image3L from "../images/piedmont/piedmont-4L.jpg"
import image1P from "../images/belvedere-firepit/glinorcal-stone-firepitP.jpg"
import image2P from "../images/bel-marin-keys/bel-marin-4P.jpg"
import image3P from "../images/piedmont/piedmont-1P.jpg"

const IndexPage = () => {
  const settings = {
    fade: true,
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    // autoplay: true,
  }
  return (
    <Layout pageInfo={{ pageName: "index" }}>
      <SEO title="Home" keywords={[`glinorcal`, `landscape`, `construction`]} />
      <Container fluid className="carousel-wrapper mx-0 px-0">
        <Media query="(max-width: 768px)">
          {matches =>
            matches ? (
              <Slider {...settings}>
                <div>
                  <img className="img img-fluid" src={image1L} alt="image1P" />
                </div>
                <div>
                  <img className="img img-fluid" src={image2L} alt="image2P" />
                </div>
                <div>
                  <img className="img img-fluid" src={image3L} alt="image3P" />
                </div>
              </Slider>
            ) : (
              <Slider {...settings}>
                <div>
                  <img className="img img-fluid" src={image1P} alt="image1" />
                </div>
                <div>
                  <img className="img img-fluid" src={image2P} alt="image2" />
                </div>
                <div>
                  <img className="img img-fluid" src={image3P} alt="image3" />
                </div>
              </Slider>
            )
          }
        </Media>
      </Container>
    </Layout>
  )
}

export default IndexPage
